      email: 'ricky.zaman@outlook.com',
      aboutMe: 'I am a programmer who enjoys building scalable web applications. My main career interests are web development, quality assurance and machine learning. ',
      experience: [
        {
          position: 'Web Developer',
          company: 'Palomino Sys',
          description: 'The role is responsible for designing, coding and modifying websites, from layout to function and according to a clients specifications. Strive to create visually appealing sites that feature user-friendly design and clear navigation.',
          startDate: 'May 2019',
          endDate: 'Present'
        },
        {
          position: 'Junior Programmer',
          company: 'Ontario Treasury Board Secretariat',
          description: 'Created and executed test cases to identify and document software defects and their causes in order to optimize CRM deployments.',
          startDate: 'August 2018',
          endDate: 'January 2019'
        },
        {
          position: 'Mobile Application Developer Internship',
          company: 'Project SZ',
          description: 'Assisted in development and implementation of mobile games as per specifications using Unity using Agile development life-cycle.',
          startDate: 'July 2017',
          endDate: 'Sept 2017'
        },
        {
          position: 'Pharmacy Technical Assistant',
          company: 'Gates Medical Centre',
          description: 'Resolved dosage conflicts and potentially harmful drug interactions by verifying patient chart with doctors in order to ensure patient safety and wellness.',
          startDate: 'September 2008',
          endDate: 'June 2010'
        }
      ]
    }
  },
  mounted () {
    const $ = this.jquery
    this.$nextTick(() => {
      // our custom jQuery code goes here
      $('a.js-scroll-trigger[href*="#"]:not([href="#"])').click(function () {
        if (location.pathname.replace(/^\//, '') === this.pathname.replace(/^\//, '') && location.hostname === this.hostname) {
          var target = $(this.hash)
          target = target.length ? target : $('[name=' + this.hash.slice(1) + ']')
          if (target.length) {
            $('html, body').animate({
              scrollTop: (target.offset().top)
            }, 1000, 'easeInOutExpo')
            return false
          }
        }
      })
      // Closes responsive menu when a scroll trigger link is clicked
      $('.js-scroll-trigger').click(function () {
        $('.navbar-collapse').collapse('hide')
      })
      // Activate scrollspy to add active class to navbar items on scroll
      $('body').scrollspy({
        target: '#sideNav'
      })
    })
  }
}
